
import { defineComponent, PropType } from 'vue';
export default defineComponent({
	props: {
		iconName: {
			type: String as PropType<
				'facebook' | 'youtube' | 'twitter' | 'pinterest' | 'instagram'
			>,
			required: true,
		},
		iconColor: {
			type: String,
			default: '#FFF',
		},
		hoverColor: {
			type: String,
			default: '#fff',
		},
	},
	data() {
		return {
			isHover: false,
		};
	},
});
