
import { defineComponent, PropType } from 'vue';

export default defineComponent({
	emits: ['click-event'],
	props: {
		kind: {
			type: String as PropType<'primary' | 'secondary'>,
			default: 'primary',
		},
		fullWidth: {
			type: Boolean,
			default: false,
		},
		disabled: {
			type: Boolean,
			default: false,
		},
	},
	methods: {
		action(): void {
			this.$emit('click-event');
		},
	},
});
